/*=========================== Burger ===========================*/
/*==============================================================*/
/*==========================================================================================*/
html {
  font-size: 10px; }
  @media (max-width: 1230px) {
    html {
      font-size: 9.5px; } }
  @media (max-width: 1150px) {
    html {
      font-size: 9px; } }
  @media (max-width: 992px) {
    html {
      font-size: 8.5px; } }
  @media (max-width: 768px) {
    html {
      font-size: 8px; } }
  @media (max-width: 568px) {
    html {
      font-size: 7.5px; } }
  @media (min-width: 992px) {
    html * {
      user-select: none; } }

body {
  box-sizing: border-box;
  font-size: 1.4rem;
  line-height: 1.4;
  color: #000;
  font-family: "Roboto", sans-serif;
  background: #f4f4f4; }
  body *,
  body *:after,
  body *:before {
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    outline: none; }
  body [data-aload],
  body [data-aload]::before,
  body [data-aload]::after {
    background-image: none !important; }
  body ul,
  body ol {
    padding: 0;
    margin: 0;
    list-style: none; }
  body a {
    text-decoration: none; }
  body a,
  body button,
  body input[type="submit"] {
    transition: ease 0.5s; }
    body a:hover,
    body button:hover,
    body input[type="submit"]:hover {
      transition: ease 0.25s; }
    body a:active,
    body button:active,
    body input[type="submit"]:active {
      transition: ease 0.15s; }
  body h1,
  body h2,
  body h3,
  body h4,
  body h5,
  body h6,
  body p {
    font-weight: normal;
    margin: 0; }

#wrapper {
  max-width: 100%;
  min-width: 320px;
  overflow: hidden;
  min-height: 100vh;
  position: relative; }
  #wrapper .site-content {
    min-height: 40vh; }
    #wrapper .site-content.mr {
      margin-top: 5rem; }

.box {
  max-width: 168rem;
  padding: 0 3rem;
  margin: 0 auto;
  width: 100%; }
  @media (max-width: 992px) {
    .box {
      padding-left: 2rem;
      padding-right: 2rem; } }
  .box-small {
    max-width: 123rem;
    padding: 0 3rem;
    margin: 0 auto;
    width: 100%; }
    @media (max-width: 992px) {
      .box-small {
        padding-left: 2rem;
        padding-right: 2rem; } }

a {
  color: #000; }
  a:hover {
    color: #939393; }

.form {
  max-width: 40rem;
  margin: 0 auto;
  padding: 3rem 0; }
  .form > * + * {
    margin-top: 2rem; }
  .form__input {
    width: 100%;
    height: 5rem;
    padding: 0 2rem;
    border: 1px solid #000;
    display: flex;
    align-items: center;
    background: #fff; }

h1 {
  text-align: center; }

#wrapper {
  padding: 4rem 0;
  display: flex;
  align-items: center;
  justify-content: center; }
